<template lang="html">
  <div class="lsgj-wrapper">
    <div class="content">
      <div id="allmap"></div>
    </div>
    <div class="dingwei">
      <p class="title">半个月定位历史</p>
      <ul class="menu">
        <li>
          <span>天津</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>石家庄</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>唐山</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>北京</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>高碑店</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>廊坊</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>张家口</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>沧州</span>
          <span>2016-11-17 18:52:10</span>
        </li>
        <li>
          <span>滨州</span>
          <span>2016-11-17 18:52:10</span>
        </li>
      </ul>
      <div class="bottom">
        <p>下载历史记录</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  mounted(){
    // 百度地图API功能
    var map = new BMap.Map("allmap");
    var point = new BMap.Point(116.331398,39.897445);
    map.centerAndZoom(point,12);

    var geolocation = new BMap.Geolocation();
    geolocation.getCurrentPosition(function(r){
      if(this.getStatus() == BMAP_STATUS_SUCCESS){
        var mk = new BMap.Marker(r.point);
        map.addOverlay(mk);
        map.panTo(r.point);
        alert('您的位置：'+r.point.lng+','+r.point.lat);
      }
      else {
        alert('failed'+this.getStatus());
      }
    },{enableHighAccuracy: true})
  }
  }
</script>

<style lang="less" scoped>
.lsgj-wrapper{
  width: 1249px;
  margin: 0 auto;
  position: relative;
  .content{
    width: 1249px;
    background: #fff;
    height: 760px;
    #allmap{
      border-top: 2px solid #415089;
      width: 1178px;
      height: 720px;
      margin: 0 30px;
    }
  }
  .dingwei{
    width: 280px;
    height: 660px;
    background: #fff;
    position: absolute;
    right: 60px;
    top: 36px;
    .title{
      padding: 18px 0 18px 20px;
      font-size: 16px;
      border-bottom: 1px solid #ccc;
    }
    .menu{
      width: 100%;
      li{
        width: 86%;
        padding: 16px 0;
        margin: 0 20px;
        border-bottom: 1px dashed #ccc;
        span:nth-of-type(2){
          float: right;
          color: #ccc;
        }
      }
    }
    .bottom{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 50px;
      background: #fafafa;
      border-top: 1px solid #ccc;
      p{
        width: 90px;
        height: 30px;
        background: #0f9a0d;
        line-height: 30px;
        text-align: center;
        float: right;
        margin: 10px 20px 20px 0;
        color: #fff;
      }
    }
  }
}
</style>



